import logo from './logo.svg';
import './App.css';
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Jumbotron } from 'react-bootstrap';
import { useState } from 'react';
import Data from './data.js';
import { Link, Route, Switch } from 'react-router-dom';
import Menu from './MenuPage.js';
import axios from 'axios';
import Sub from './subData.js';

function App() {

  let [ menu, menu변경 ] = useState(Data);
  let [ more, more변경 ] = useState(true);
  let [ sub, sub변경 ] = useState(Sub);
  let [ side추가, side추가변경] = useState(); 

  return (
    <div className="App">

      <Navbar bg="light" expand="lg" className="">
        <Navbar.Brand as={Link} to="/"> BC.FoodShop </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/"> Home </Nav.Link>
            <Nav.Link as={Link} to="/menu"> Menu </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
       
      <Switch>

        <Route exact path="/" >
          <Jumbotron className="background"/>
          <div className="container">
          <div className="row">
            {
              menu.map((data, i)=>{
                return <MenuCon menu={menu[i]} i={i} key={i}/>
              })
            }
          </div>

          {
            more == true
            ? (
              <button className="btn btn-primary mt-5" onClick={()=>{

                axios.get("https://raw.githubusercontent.com/EnvyMON/dbgmenu/main/data3.json")
                .then((result)=>{
                  console.log("성공!")
                  console.log(result.data)
                  menu변경([...menu, ...result.data]);
                  more변경(false);
                })
                .catch(()=>{
                  console.log("실패!")
                });
              
              }}> 메뉴 더보기 </button>
            )
            : null
          }
            
          </div>
        </Route>

        <Route path="/menu/:id">
            <Menu menu={menu} sub={sub}/>
        </Route>
        
        <Route path="/:id"> 
            <div> 아무거나 </div>
        </Route>
      
      </Switch>

    </div>
  );
}

function MenuCon(props){
  return(
    <div className="col-md-4">
      <img src={'https://www.yupdduk.com/images/menu/' + props.menu.text} width="100%"/>
      <h4> {props.menu.title} </h4>
      <p> {props.menu.content} </p>
      <p> {props.menu.price} 원</p>
    </div>
  )
}




export default App;
