import { useEffect } from "react";
import { useState } from "react";
import { useHistory, useParams } from "react-router";
import styled from 'styled-components';
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Jumbotron } from 'react-bootstrap';



let 박스 = styled.div`
    padding: 20px;
`;
let 제목 = styled.h4`
    text-align: center;
    font-size: 25px;
    color: ${ props => props.색상 };
`;

function Menu(props){

    let [ alert, alert변경 ] = useState(true);

    useEffect(()=>{
        let 타이머 = setTimeout(()=>{ alert변경(false) }, 2000)
        return ()=>{ clearTimeout(타이머) }
    },[]);
    // [alert] 라고 스태이트변수를 넣어주면 alert변수가 변할때만 update 발생
    // [] 라고 빈칸을 놔두면 페이시 처음 랜더링 할때만 실행 됨.

    let history = useHistory();
    let { id } = useParams();
    let 찾은상품 = props.menu.find(function(상품){
        return 상품.id == id;
    })

    return (
            <div className="container">
                <박스>
                    <제목 색상={'black'}> 선택 메뉴 </제목>
                    <hr></hr>
                </박스>
            {
                alert == true 
                ? (<div className="my-alert2">
                    <p> 현재 MBC '나혼자산다' 방영 중! </p>
                  </div>)
                : null
            }
            <div className="row">
                <div className="col-md-6">
                <img
                    src={"https://www.yupdduk.com/images/menu/" + 찾은상품.text}
                    width="100%"
                />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5"> {찾은상품.title} </h4>
                    <p> {찾은상품.content} </p>
                    <p> {찾은상품.price} 원 </p>
                    <button className="btn btn-primary" onClick={()=>{
                        // history.goBack();
                        history.push('/');
                    }}> 목록으로 </button> &nbsp;
                    <button className="btn btn-danger"> 주문하기 </button>
                </div>
            </div>

            <h4 className="subMenuTitle">추가 메뉴</h4><hr></hr>        
            <div className="row subMenu">
                {
                    props.sub.map((data, i)=>{
                        return <SubMenu sub={data} i={i} key={i}/>
                    })
                }
            </div>

            </div>
    )
}

function SubMenu(props){
    return(
        <div className="col-md-4">
            <img src={props.sub.text} width="100%"/>
            <h4> {props.sub.title} </h4>
            <p> {props.sub.price} 원</p>
            <Form>
                <Form.Group controlId="exampleForm.SelectCustom">
                    <Form.Control as="select" custom >
                        <option>0</option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                    </Form.Control>
                </Form.Group>
            </Form>
        </div>
    )
}



export default Menu;
